import React from 'react'
import HoverVideoPlayer from 'react-hover-video-player';
import './Work.css'
function Work() {
    return (
        <div className="work">
            <div className="work_heading">
                <h5>Featured Work.</h5>
            </div>

            <div className="featured_work">
                <div className="zyppys">
                    <img className="zyppys_img" src={process.env.PUBLIC_URL + '/images/car.jpg'}/>  
                    <img className="zyppys_logo" src="//images.ctfassets.net/b62jgkkofmz2/53QqxmKvGdR14P2C8z3q2X/805effeb1876ec61214663bd80684fde/logo-with-text.svg"/>
                    <p className="zyppys_para">Building India's first ever rental car marketplace.</p>                 
                </div>
                
                <div className="hl">
                    {/* <video className="phone_video" src= {process.env.PUBLIC_URL + '/videos/phone_video.mp4'} preload/> */}
                    {/* <div className="phone_video"> */}
                    <HoverVideoPlayer className="phone_video"
                       videoSrc={process.env.PUBLIC_URL + '/videos/phone_video.mp4'}
                       
                       loadingOverlay={
                         <div className="loading-spinner-overlay" />
                       }
                     />
                     {/* </div> */}
                    <img className="hl_logo" src="//images.ctfassets.net/b62jgkkofmz2/4taqxDkC14IjVNKhbVkYE8/aa3814a58b3a976dd293b4b581b08a6c/black.svg" />
                    <p className="hl_para">A decade old news channel's makeover</p>
                </div>
            </div>
        </div>
    )
}

export default Work
